@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-arr-l:before { content: ""; }

.icon-arr-r:before { content: ""; }

.icon-checked:before { content: ""; }

body { font-family: Ubuntu, Arial, sans-serif; font-size: 16px; line-height: 1; }

.inner { max-width: 800px; padding: 0 20px; margin: 0 auto; }

.btn { height: 46px; line-height: 46px; color: white; font-family: "Ubuntu", sans-serif; font-size: 22px; font-weight: 500; text-align: center; position: relative; cursor: pointer; }

.btn:hover { opacity: 0.9; transition: all 0.4s; }
.btn:active { top: 1px; }
.btn_next { width: 156px; }
.btn_next i { font-size: 15px; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); margin: auto; z-index: 10; }
.btn_quiz { width: 156px; padding-left: 17px; }
.btn_quiz:active { top: inner; }
.btn_again { width: 164px; background-color: rgba(153, 153, 153, 0.7); padding-left: 17px; }
.btn_quiz:hover, .btn_again:hover { background-color: #e1272c; transition: all 0.3s; }
.btn_quiz i, .btn_again i { font-size: 15px; position: absolute; top: 50%; left: 13px; transform: translateY(-50%); margin: auto; z-index: 10; }
.btn.btn_red { background-color: #e1272c; }
.btn.btn_red:active { background-color: #CE2025; }

.header { min-height: 50vh; width: 100%; background: url(../img/header.jpg) no-repeat center; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; text-align: center; position: relative; }
@media only screen and (max-height: 450px) { .header { min-height: 350px; } }
.header.header_sm { padding-bottom: 7.8125%; min-height: inherit; height: 0; }
.header.header_sm .header__top { position: absolute; left: 0; right: 0; top: 50%; transform: translateY(-50%); padding: 0; }
@media only screen and (max-width: 1024px) { .header.header_sm { padding-bottom: 80px; } }
.header.header_sm .header__pic { width: 226px; height: 226px; background-color: white; border-radius: 50%; overflow: hidden; position: absolute; left: 0; right: 0; top: 44.7%; margin: auto; z-index: 300; }
@media only screen and (max-width: 1024px) { .header.header_sm .header__pic { top: 38px; } }
@media only screen and (max-width: 750px) { .header.header_sm .header__pic { width: 175px; height: 175px; } }
.header.header_sm .header__pic img { position: absolute; top: 0; bottom: 0; left: 0; right: 0; margin: auto; }
.header .inner { top: 0; bottom: 0; left: 0; right: 0; position: absolute; max-width: none; }
.header__top { position: relative; overflow: hidden; margin: auto; font-family: "Ubuntu", sans-serif; font-size: 18px; font-weight: 400; line-height: 1; color: #fff; padding-top: 33px; }
@media only screen and (max-width: 500px) { .header__top { padding-top: 20px; font-size: 16px; } }
.header__top span { position: relative; display: inline-block; padding: 0 38px; text-transform: uppercase; }
@media only screen and (max-width: 700px) { .header__top span { padding: 0 20px; } }
.header__top span:before, .header__top span:after { content: ""; display: block; position: absolute; top: 0; bottom: 0; margin: auto; width: 152%; height: 1px; background-color: #fff; opacity: 0.3; z-index: 100; }
@media only screen and (max-width: 700px) { .header__top span:before, .header__top span:after { width: 75%; } }
.header__top span:before { left: -152%; }
@media only screen and (max-width: 700px) { .header__top span:before { left: -75%; } }
.header__top span:after { right: -152%; }
@media only screen and (max-width: 700px) { .header__top span:after { right: -75%; } }
.header__title { position: absolute; top: 50%; left: 0; right: 0; transform: translateY(-50%); width: 100%; padding: 0 20px; }
@media only screen and (max-width: 800px) { .header__title { font-size: 33px; } }
@media only screen and (max-width: 500px) { .header__title { font-size: 27px; } }
.header .btn_quiz { position: absolute; bottom: -23px; left: 0; right: 0; margin: auto; z-index: 200; }
@media only screen and (max-width: 500px) { .header .btn_quiz { height: 40px; line-height: 40px; font-size: 19px; } }
.header .btn_quiz:active { top: auto; }

.main { position: relative; background-color: #FFF; }
.main.main_no-pad .inner { padding-top: 0; padding-bottom: 0; }
.main .inner { padding-top: 84px; padding-bottom: 50px; }

.answer { position: relative; text-align: center; }
.answer__title { padding: 58px 28px 56px 46px; position: relative; color: #333; font-family: Ubuntu; font-size: 32px; font-weight: 400; line-height: 44px; text-align: left; }
@media only screen and (max-width: 650px) { .answer__title { font-size: 22px; padding-left: 28px; padding-bottom: 30px; } }
.answer__title .count { color: #d9d9d9; font-size: 32px; font-family: Ubuntu, sans-serif; font-weight: 400; position: absolute; left: 5px; }
@media only screen and (max-width: 650px) { .answer__title .count { font-size: 22px; } }
.answer__sev { font-family: Ubuntu, sans-serif; font-size: 18px; font-weight: 500; line-height: 1; color: #d9d9d9; text-align: center; margin-bottom: 32px; position: absolute; bottom: 25px; left: 0; right: 0; margin: auto; }
.answer .btn_next { margin-bottom: 40px; }

.items { position: relative; font-size: 0; }
.items__wrap { margin: 0 -1%; }
@media only screen and (max-width: 650px) { .items__wrap { margin: 0; } }
.items .item { background-color: #d9d9d9; width: 48%; min-height: 140px; position: relative; display: inline-block; vertical-align: top; transition: background-color 0.3s; margin: 0 1% 40px; text-align: center; }
@media only screen and (max-width: 750px) { .items .item { margin-bottom: 20px; } }
@media only screen and (max-width: 650px) { .items .item { margin-bottom: 20px; } }
@media only screen and (max-width: 760px) { .items .item.item_pic { width: 48%; min-height: 140px; } }
@media only screen and (max-width: 500px) { .items .item.item_pic { width: 98%; } }
.items .item.item_pic:after { display: none; }
.items .item.item_pic:before { content: ""; display: block; width: 100%; height: 100%; position: absolute; top: 0; left: 0; z-index: 100; opacity: 0; }
.items .item.item_pic:hover:before, .items .item.item_pic.is-answered:before { opacity: 1; transition: opacity 0.3s; }
.items .item.item_pic.is-answered .item__checked { display: block; }
.items .item.item_pic:nth-child(1) { background: url(../img/img1.jpg) no-repeat center; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
.items .item.item_pic:nth-child(1):before { background-color: rgba(0, 137, 197, 0.7); }
.items .item.item_pic:nth-child(2) { background: url(../img/img2.jpg) no-repeat center; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
.items .item.item_pic:nth-child(2):before { background-color: rgba(93, 161, 77, 0.7); }
.items .item.item_pic:nth-child(3) { background: url(../img/img3.jpg) no-repeat center; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
.items .item.item_pic:nth-child(3):before { background-color: rgba(179, 0, 119, 0.7); }
.items .item.item_pic:nth-child(4) { background: url(../img/img4.jpg) no-repeat center; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
.items .item.item_pic:nth-child(4):before { background-color: rgba(255, 153, 0, 0.7); }
.items .item .item__checked { font-size: 20px; color: white; display: none; z-index: 100; position: absolute; left: 16px; top: 14px; }
@media only screen and (max-width: 700px) { .items .item { min-height: 175px; } }
@media only screen and (max-width: 500px) { .items .item { width: 100%; min-height: 140px; margin-left: 0; margin-right: 0; } }
.items .item:after { content: ""; display: block; width: 0; height: 0; border-top: 28px solid transparent; border-right: 28px solid transparent; position: absolute; bottom: -28px; left: 62px; transition: all 0.3s; }
@media only screen and (max-width: 750px) { .items .item:after { display: none; } }
.items .item.is-active .item__checked { display: block; }
.items .item.is-active, .items .item:hover { background-color: #0089c5; }
.items .item.is-active .item__text, .items .item:hover .item__text { color: white; }
.items .item.is-active:after, .items .item:hover:after { border-top-color: #0089c5; }
.items .item__text { margin: auto; color: #333; font-family: Ubuntu; font-size: 22px; font-weight: 400; line-height: 32px; padding: 38px 40px; text-align: center; }
.items .item__text .is-left { text-align: left; }
@media only screen and (max-width: 650px) { .items .item__text { font-size: 19px; } }

.finish { position: relative; text-align: center; }
.finish .inner { padding-top: 205px; padding-bottom: 100px; }
@media only screen and (max-width: 750px) { .finish .inner { padding-top: 160px; } }
.finish__title { color: #e1272c; font-family: Ubuntu, sans-serif; font-size: 36px; font-weight: 400; line-height: 130%; margin-bottom: 55px; }
@media only screen and (max-width: 750px) { .finish__title { margin-bottom: 30px; } }
@media only screen and (max-width: 600px) { .finish__title { font-size: 32px; margin-bottom: 30px; } }
@media only screen and (max-width: 400px) { .finish__title { font-size: 28px; } }
.finish__text { text-align: left; }

.quiz h1 { color: white; font-family: Ubuntu; font-size: 40px; font-weight: 400; line-height: 130%; letter-spacing: -0.5px; }
.quiz a { text-decoration: none; transition: all 0.4s; }
.quiz p { color: #333; font-size: 18px; font-weight: 300; line-height: 30px; font-family: "Ubuntu", sans-serif; }
.quiz img { max-width: 100%; height: auto; }

.social { font-size: 0; text-align: center; }
.social a { transition: all 0.4s; width: 60px; height: 60px; border-radius: 50%; background-color: #d9d9d9; display: inline-block; position: relative; color: #fff; font-size: 35px; margin: 50px 14px 0; }
.social a:hover:first-child { background-color: #3b5998; }
.social a:hover:last-child { background-color: #1dadeb; }
.social a i { position: absolute; top: 50%; top: 52%; left: 50%; transform: translate(-50%, -50%); display: block; z-index: 10; }
.social a i.fa-twitter { left: 52%; }

.social-btn { max-width: 552px; position: relative; margin: 51px auto 30px; }
.social-btn__wrp { margin: 0 -4px; font-size: 0; }
.social-btn__col { display: inline-block; vertical-align: top; width: 272px; margin: 0 4px; }
@media only screen and (max-width: 600px) { .social-btn__col { display: block; margin: auto; } }
@media only screen and (max-width: 350px) { .social-btn__col { width: 260px; } }
.social-btn a { height: 46px; line-height: 44px; position: relative; display: block; margin-bottom: 10px; }
.social-btn a:hover { opacity: 0.8; }
.social-btn a:active { top: 1px; }
.social-btn a span { display: inline-block; vertical-align: middle; font: 300 18px/1 "Ubuntu", sans-serif; color: #fff; margin-left: 18px; }
.social-btn a i { position: absolute; top: 50%; transform: translateY(-50%); left: 31px; margin: auto; z-index: 100; font-size: 25px; color: white; }
.social-btn__fb { background-color: #3b5998; }
.social-btn__tw { background-color: #1dadeb; }

.arrows { display: block; }
.arrows .arrow { display: inline-block; position: absolute; top: 50%; transform: translateY(-50%); margin: auto; color: white; font-size: 26px; }
.arrows .arrow-left { left: 35px; }
.arrows .arrow-right { right: 35px; }

.progress { position: relative; height: 7px; background-color: #d9d9d9; }
.progress__line { position: absolute; top: 0; bottom: 0; left: 0; }


